html,
body,
#root {
  height: 100%;
  margin: 0;
  padding: 0;
  background: #000;
  font-family: "Archivo Black", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-style: normal;
}


/* 🔹 App Layout */
.app-container {
  position: relative;
  height: 85vh;
  width: 100vw;
}

/* 🔹 Top Banner */

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100px;
  flex-direction: column;
  display: flex;
  justify-content: center;
  background: rgba(20, 20, 20, 0.8);
  color: white;
  z-index: 1000;
  padding: 0 1rem;
  backdrop-filter: blur(6px);
}

.header p{
 padding-top: 50px;
 font-size: 14px;;
 text-align: left;
}


.header h1{
 padding-top: 15px;
}

.banner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 0 1rem;
}

.banner-title {
  font-size: 1.2rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-align: left;
  flex: 1;
  display: inline-flex;
  align-items: center;
}
.banner-title img {
 margin-right: 8px;
 width: 35px;
 border-radius: 50%;
}

/* 🔹 Social Links */
.socials {
  position: absolute;
  right: 1rem;
  display: flex;
  gap: 1rem;
  margin-top: 20px;
}

.social-link img {
  width: 24px;
  height: 24px;
}

.social-link:hover img {
  opacity: 1;
  transform: scale(1.1);
}

/* 🔹 Map */
.map-container {
  margin-top: 100px;
  height: calc(85% - 100px);
  width: 100%;
}

.custom-marker img {
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.8);
}

/* 🔹 Footer */
.footer {
  
  height: 100px;
  background: rgba(20, 20, 20, 0.8);
  color: #ccc;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  font-size: 0.9rem;
  backdrop-filter: blur(6px);
  z-index: 1000;
}

.footer a {
  color: #00bcd4;
  text-decoration: none;
  margin-left: 0.3rem;
}

.footer a:hover {
  text-decoration: underline;
}

.footer p {
  margin: 5px 0;
  font-size: 12px;
}

.contact_link {
  color:#ffffff !important;
  text-decoration: none;
}

.billboard-wrapper{
  position: relative;
     left: 10%;
     top:10% ;
      z-index: 10000;
      max-width: 300px;
}
.total-billboard-info{
  position: relative;
  top: 5%;
  left: 55%;
  z-index: 10001;
  padding: 8px 12px ;
  border-radius: 10px;
  background-color: #1EF3A5;
  color: #333;
  width: 170px;
}


 .billboard{
      position:relative;
      width:300px;
      height:100px;
      background:#000;
      border:4px solid #fff;
      border-radius:12px;
      box-shadow:0 6px 16px rgba(0,0,0,.15);
      display:flex;
      align-items:center;
      justify-content:center;   
      overflow:hidden;
    }
    .billboard video{
      position:absolute;
      top:0; left:0;
      width:100%; height:100%;
      object-fit:cover;
      z-index:0;
      filter:brightness(0.9);
    }
    .msg{
      position:relative;
      font-weight:800;
      color:#fff;
      font-size:16px;
      text-align:center;
      text-shadow:0 2px 4px rgba(0,0,0,.6);
      z-index:1;
    }
    .pole{
      width:18px;
      height:300px;
      background:linear-gradient(180deg,#fff 0%,#3b3f46 60%,#31353b 100%);
      border-radius:0;
      margin-top:-10px;
      box-shadow:0 6px 20px rgba(0,0,0,.25);
      margin: 0 auto;
    }
    .shadow{
      width:140px;
      height:20px;
      background:radial-gradient(closest-side,rgba(0,0,0,.18),transparent 80%);
      border-radius:50%;
      filter:blur(6px);
      margin-top:8px;
    }



.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #52D593;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.custom-number-icon {
  background-color: #007bff;
  color: white;
  border-radius: 50%;
  text-align: center;
  font-weight: bold;
  border: 2px solid white;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
}

.marker-number {
  width: 25px;
  height: 25px;
  line-height: 26px;
  font-size: 14px;
  text-align: center;
}